.layout {
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(275px, 1fr));

  .cell {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;

    background-color: #c3c3c3;

    border-radius: 3px;
    padding: 20px;

    .post-mata {
      a {
        font-size: 12px;

        text-decoration: none;
        background-color: #f7f7f7;
        padding: 3px 5px;
        margin-right: 5px;
      }
    }
    h2 {
      font-size: 25px;
    }
    p {
      font-size: 15px;
    }
  }
}
